<script lang='ts'>
  import { infoShow, dataCoin } from './InfoData.svelte';
  import { stateUser } from './InfoBox.svelte';

  export let nameCoin: string;
  export let nameBadge: string;
  export let buy: any;
  export let sell: any;

  let coinValue: any = '1';
  let coinBuy: any;
  const convertBuy = (value: any) => {
    if (value == '') return coinBuy = '';
    coinBuy = value * buy;
    $dataCoin.monto = value;
    $dataCoin.value = coinBuy;
    $dataCoin.buy = nameBadge;
    $dataCoin.sell = nameCoin;
    return coinBuy;
  };

  let coinSell: any;
  const convertSell = (value: any) => {
    if (value == '') return coinSell = '';
    coinSell = value * sell;
    $dataCoin.monto = value;
    $dataCoin.value = coinSell;
    $dataCoin.buy = nameBadge;
    $dataCoin.sell = nameCoin;
    return coinSell;
  }

  $: if (buy !== '') {
    convertBuy(coinValue);
    convertSell(coinValue);
  }
</script>
<div class='box-main'>
  <span
    style:color=#fff
    style:font-size=.8em
    style:margin-bottom=.7em
  >Compra: <strong style:color=#000>{buy}</strong> Venta: <strong style:color=#000>{sell}</strong></span>
  {#if $stateUser === 'buy'}
    <div class='item-box'>
      <span>{nameBadge}</span>
      <input name='name-coin' bind:value={coinValue} on:keyup={() => convertBuy(coinValue)} autocomplete='off'>
    </div>
    <div class='item-box'>
      <span>{nameCoin}</span>
      <input name='name-sell' value={coinBuy} disabled>
    </div>
    <button on:click={() => {$infoShow = 'show-data'; convertBuy(coinValue)}}>Comprar</button>
  {:else if $stateUser === 'sell'}
    <div class='item-box'>
      <span>{nameBadge}</span>
      <input name='name-coin' bind:value={coinValue} on:keyup={() => convertSell(coinValue)} autocomplete='off'>
    </div>
    <div class='item-box'>
      <span>{nameCoin}</span>
      <input name='name-sell' value={coinSell} disabled>
    </div>
    <button on:click={() => {$infoShow = 'show-data'; convertSell(coinValue)}}>Vender</button>
  {/if}
</div>

