// @ts-check
import { sucess } from './InfoData.svelte';
import { isValidPhoneNumber } from 'libphonenumber-js';

/** @param {Event | any} e*/
const sendData = async e => {
	e.preventDefault();

	const datas = new FormData(e.target);
	/** @type any*/
	let valueInput;
	e.target.childNodes[0].childNodes.forEach(i => {
		if (i.nodeName === 'INPUT' && i.name === 'number') valueInput = i.value;
	});

	if (numberValidator(valueInput)) {
		const ctx = await fetch('https://apiverify.deno.dev/data/message', {
			method: 'POST',
			body: datas,
		});
		sucess.update(str => str = 'confirm');
	}
}

const numberValidator = number => {
	return isValidPhoneNumber(number, 'AR');
}

export {
	sendData,
}
