<script lang='ts'>
  import { AsYouType } from 'libphonenumber-js';
  import { v4 } from 'uuid';
  import { sendData } from './sendMenssage.js';
  import Button from './Button.svelte';
  let data: any;
  const formatterNumber = (e: any) => {
    data = new AsYouType('AR').input(e.target.value);
  }
</script>

<script context='module' lang='ts'>
  import { writable } from 'svelte/store';
  export const sucess = writable('pending');
  export const infoShow = writable('');
  export const dataCoin = writable({
    token: '',
    monto: '',
    value: '',
    buy: '',
    sell: '',
  });
</script>

<aside class={$infoShow}>
  <Button drn='right' pos='absolute' ctx='close' size={3}
    on:click={() => {
      $infoShow = '';
      $sucess = 'pending';
      data = '';
    }}
  />
  {#if $sucess === 'pending'}
    <h2>Complete sus datos porfavor</h2>
    <form on:submit={sendData} novalidate>
      <fieldset>
        <legend>Complete sus datos</legend>
        <label for='number'>Numero</label>
        <input bind:value={data} name='number' placeholder='Inserte su numero' on:keyup={formatterNumber} autocomplete='off'>
        <input type='hidden' name='monto' value={$dataCoin.monto}>
        <input type='hidden' name='value' value={$dataCoin.value}>
        <input type='hidden' name='coinBuy' value={$dataCoin.buy}>
        <input type='hidden' name='coinSell' value={$dataCoin.sell}>
        <input type='hidden' name='token' value={v4()}>
        <button>Enviar</button>
      </fieldset>
    </form>
  {:else}
    <h2>Gracias por usar nuestros servicios</h2>
    <p>En 2 o 5 minutos le llegara un mensaje con todos los datos e informacion.</p>
  {/if}
</aside>

