import { writable } from 'svelte/store';

let dolar = writable({
	buy: '',
	sell: '',
});

(async () => {
	const data = await fetch('https://api.bluelytics.com.ar/v2/latest');
	const {blue: {value_buy, value_sell}} = await data.json();
	dolar.update(coin => {
		coin.buy = value_buy;
		coin.sell = value_sell;
		return coin;
	});
})();

let cripto = writable({
	buy: '',
	sell: '',
});

(async () => {
	const data = await fetch ('https://api.coingecko.com/api/v3/simple/price?ids=bitcoin&vs_currencies=btc%2Cusd');
	const {bitcoin: {usd}} = await data.json();
	cripto.update(coin => {
		coin.buy = usd;
		coin.sell = usd + 500;
		return coin;
	})
})();

export {
	dolar,
	cripto,
}
